<template>
  <v-row no-gutters>
    <v-col lg="12" sm="12">
      <base-card ref="panelCol" class="panelCol-event">
        <v-form
            ref="BotForm"
            lazy-validation
        >

          <v-card-text v-if="!Bot.Bot && !bussy">
            <v-row justify="center">
              <v-col cols="12">
                <v-card-title class="px-0 justify-center" v-if="Bot.id == null">
                      
                </v-card-title>
              </v-col>
              <v-col md="4" class="flex flex-column align-center">
                <v-tooltip top :disabled="disabledBot.spot === false" max-width="300">
                  <template v-slot:activator="{ on }">
                    <div class="col" v-on="on">
                      <v-btn
                          block
                          :disabled="disabledBot.spot"
                          @click="Bot.Bot = 'spot'"
                          value="spot"
                      ></v-btn>
                    </div>
                  </template>
                  <span>        </span>
                </v-tooltip>
                <v-tooltip top :disabled="disabledBot.futures === false" max-width="300">
                  <template v-slot:activator="{ on }">
                    <div class="col" v-on="on">
                      <v-btn block :disabled="disabledBot.futures" @click="Bot.Bot = 'futures'" value="futures"> ()</v-btn>
                    </div>
                  </template>
                </v-tooltip>
                <v-tooltip top :disabled="disabledBot.futurespro === false" max-width="300">
                  <template v-slot:activator="{ on }">
                    <div class="col" v-on="on">
                      <v-btn block :disabled="disabledBot.futurespro" @click="Bot.Bot = 'futurespro'" value="futurespro"> (PRO)</v-btn>
                    </div>
                  </template>
                </v-tooltip>
              </v-col>
            </v-row>
          </v-card-text>
          <v-card-text v-if="Bot.Bot">
            <v-row>
              <v-col cols="12" lg="6">
                <v-card-title class="px-0" v-if="Bot.id === null">
                    
                </v-card-title>
                <v-card-title class="px-0" v-else>
                   - {{ Bot.Name }}
                  <v-chip
                      v-if="Bot.Status === 'active'"
                      class="ml-4"
                      color="success"
                      label
                      small
                      text-color="white"
                  >
                    <v-icon small left>mdi-check</v-icon>
                    
                  </v-chip>
                  <v-chip
                      v-else-if="Bot.Status === 'paused'"
                      class="ml-4"
                      color="warning"
                      label
                      small
                      text-color="white"
                  >
                    <v-icon class="mr-2" size="15">mdi-pause</v-icon>
                    
                  </v-chip>
                  <v-chip
                      v-else
                      class="ml-4"
                      color="info"
                      label
                      small
                      text-color="white"
                  >
                    <v-progress-circular
                        indeterminate
                        :value="100"
                        color="white"
                        :size="15"
                        :width="2"
                        class="mr-2"
                    ></v-progress-circular>
                    
                  </v-chip>
                </v-card-title>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                        v-model="Bot.Name"
                        label=""
                        :rules="[v => !!v || ' ']"
                        required
                        outlined
                        hide-details
                    />
                  </v-col>
                  <v-col sm="6">
                    <v-select
                        v-model="Bot.PriceDriver"
                        outlined
                        label=""
                        :items="priceDrivers"
                        @change="getMarkets"
                        :rules="[(v) => !!v || ' ']"
                        hide-details
                    />
                  </v-col>
                  <v-col sm="6">
                    <v-select
                        v-model="Bot.Market"
                        outlined
                        label=""
                        no-data-text="  "
                        :items="markets"
                        :rules="[(v) => !!v || ' ']"
                        hide-details
                    />
                  </v-col>
                </v-row>
              </v-col>
              <v-col lg="6">
                <CSTM
                    :Bot="Bot"
                    :markets="markets"
                    :binanceMarkets="binanceMarkets"
                    v-if="Bot.Bot === 'futures'"
                    ref="CSTM"
                />

              </v-col>
            </v-row>
          </v-card-text>
          <v-card-text align="center" v-if="Bot.Bot">
            <v-btn color="success" @click="saveBot" :disabled="bussy"></v-btn>
            <v-btn v-if="Bot.Status === 'paused'" color="primary" @click="botStart" class="ml-4"></v-btn>
            <v-btn v-if="Bot.Status === 'active'" color="primary" @click="botStop" class="ml-4"></v-btn>
            <v-btn v-if="Bot.Status === 'pending'" color="primary" disabled class="ml-4"></v-btn>
          </v-card-text>
        </v-form>
      </base-card>
    </v-col>
    <v-col lg="12" md="12" ref="graphCol" @resize="onResize" v-if="Bot.Bot">
      <futureGraph class="mt-4" :width="graphWidth" :height="graphHeight" :symbolName="Bot.symbolName" :apiKey="apiKey"
                   :apiSecret="apiSecret"/>
    </v-col>
    <v-col cols="12" v-if="Bot.id !== null">
      <base-card class="mt-4">
        <v-card-title>
          
        </v-card-title>

        <v-card-text>
          <v-data-table
              :headers="ordersHeaders"
              :items="orders"
              no-data-text="  "
          >
            <template v-slot:item.time="{item}">
              {{ item.time }}
            </template>

            <template v-slot:item.positionSide="{item}">
              <v-chip
                  v-if="item.positionSide === 'LONG'"
                  color="success"
                  small
              >{{ item.positionSide }}
              </v-chip>

              <v-chip
                  v-if="item.positionSide === 'SHORT'"
                  color="error"
                  small
              >{{ item.positionSide }}
              </v-chip>
            </template>
          </v-data-table>
        </v-card-text>
      </base-card>
    </v-col>
  </v-row>

</template>

<script>
import axios from "axios";
import firebase from "firebase";
import "firebase/database";
import Binance from 'binance-api-node';
import futureGraph from '@/views/app/dashboard/FutureGraph';
import CSTM from "@/views/app/bots/CSTM";

const client = Binance();
const firestore = firebase.firestore();
const database = firebase.database();

export default {
  components: {futureGraph, CSTM},
  data() {
    return {
      priceDrivers: [],
      priceDriver: null,
      markets: [],
      binanceMarkets: null,
      Bot: {
        Bot: false,
        id: null
      },
      graphWidth: 333,
      graphHeight: 500,
      // symbolName: 'BTCUSDT',
      ordersHeaders: [
        {text: "", value: "time"},
        {text: "", value: "market"},
        {text: "", value: "positionSide"},
        {text: "", value: "price"},
        {text: "", value: "origQty"},
      ],
      orders: [],
      bussy: false,
      needsUpdate: false,
      apiKey: null,
      apiSecret: null,
      disabledBot: {
        spot: true,
        futures: true,
        futurespro: true
      }
    }
  },
  mounted() {
    window.addEventListener('resize', this.onResize);

    this.needsUpdate = true;
  },
  destroyed() {
    window.removeEventListener('resize', this.onResize);
  },
  methods: {
    async onResize(event) {
      if (typeof this.$refs.graphCol !== 'undefined') {
        this.graphWidth = this.$refs.graphCol.clientWidth;
      }

      // if(this.$refs.panelCol.$el.clientHeight !== undefined) {
      //   this.graphHeight = this.$refs.panelCol.$el.clientHeight - 34;
      // }
    },

    async getMarkets(e) {
      /* Get Markets for v-select { */

      var priceDriver = this.priceDrivers.find(obj => {
        return obj.value === e
      });
      this.priceDriver = priceDriver;

      /* Formating platform for API */
      var platform = '';
      // Binance
      if (priceDriver.platform === 'Binance') {
        if (priceDriver.type === 'Leverage Trading') {
          platform = 'BinanceFutures'
        }
      }

      /* Doing ajax to get markets from Haas */
      if (platform.length > 0) {
        axios.get('https://sota-api.gq/GetPriceMarkets/' + platform).then(response => {
          if (response.data.length > 0) {
            response.data.map(el => {
              this.markets.push({
                text: el.ShortName,
                value: el.ShortName,
                PrimaryCurrency: el.PrimaryCurrency,
                SecondaryCurrency: el.SecondaryCurrency,
                MinimumTradeAmount: el.MinimumTradeAmount
              });
            });
          }
        });

        /* Doing request to Binance API */
        this.binanceMarkets = await client.futuresPrices();
      }
      /* } Get Markets for v-select */
    },

    async saveBot() {
      /* Save Bot { */
      if (!this.bussy) {
        this.bussy = true;
      } else if (this.bussy === true) {
        return false;
      }

      if (!this.$refs.BotForm.validate()) {
        setTimeout(() => {
          this.bussy = false;
        }, 500);
        return false;
      }

      if(this.Bot.Bot === 'futures') await this.$refs.CSTM.saveCSTM();

      // Redirect to new exist bot
      await this.$router.push({name: 'Bot', params: {id: this.Bot.id}});
      this.needsUpdate = true;
      this.bussy = false;
      /* } Save Bot */
    },

    async botStart() {
      firebase.auth().onAuthStateChanged(async user => {
        if (this.Bot.id !== null) {
          var Bot = await firestore.collection('users').doc(user.uid).collection('Bots').doc(this.Bot.id);

          if (Bot) {
            Bot.update({
              Status: 'pending'
            });
            database.ref('tasks').push().set({
              task: 'bot_start',
              user: user.uid,
              data: {
                id: Bot.id
              }
            });
          }
        }
      });
    },

    async botStop() {
      firebase.auth().onAuthStateChanged(async user => {
        if (this.Bot.id !== null) {
          var Bot = await firestore.collection('users').doc(user.uid).collection('Bots').doc(this.Bot.id);

          if (Bot) {
            Bot.update({
              Status: 'pending'
            });
            database.ref('tasks').push().set({
              task: 'bot_stop',
              user: user.uid,
              data: {
                id: Bot.id
              }
            });
          }
        }
      });
    },

    async getOrders() {
      if (this.apiKey && this.apiSecret) {
        const client2 = Binance({
          apiKey: this.apiKey,
          apiSecret: this.apiSecret,
          // getTime: await client.time(),
        })

        let futuresUserTrades = await client2.futuresAllOrders({
          symbol: this.Bot.symbolName,
          // status: 'active'
        });

        this.orders = [];
        await futuresUserTrades.map(el => {
          if (el.status === "NEW") {
            let time = new Date(el.time);

            this.orders.push({
              time: time.toLocaleString().replace(',', ''),
              market: this.Bot.Market,
              positionSide: el.positionSide,
              price: el.price,
              origQty: el.origQty
            });
          }
        });
      } else {
        this.orders = [];
      }
    }
  },
  watch: {
    'Bot.Bot': function() {
      setTimeout(() => {
        this.onResize();
      }, 500);
    },
    'Bot.PriceDriver': function (val, oldval) {
      this.getMarkets(val);

      let priceDriver = this.priceDrivers.find(obj => {
        return obj.value === val
      });

      this.apiKey = priceDriver.apiKey;
      this.apiSecret = priceDriver.apiSecret;

      this.getOrders();
    },
    'Bot.symbolName': async function (val, oldval) {
      this.getOrders();
    },
    'Bot.Market': function() {
      this.getOrders();
    },
    'needsUpdate': function () {
      firebase.auth().onAuthStateChanged(async user => {
        // Price Drivers {
        var priceDrivers = firestore.collection('users').doc(user.uid).collection('PriceDrivers');
        priceDrivers.onSnapshot(snapshot => {
          this.priceDrivers = [];
          snapshot.forEach(doc => {
            if(doc.data()['AccountType'] === 'Leverage Trading') {
              this.disabledBot.futures = false;
              this.disabledBot.futurespro = false;
            }
            if(doc.data()['AccountType'] === 'Spot Trading') this.disabledBot.spot = false;

            this.priceDrivers.push({
              text: doc.data()['AccountName'],
              value: doc.id,
              type: doc.data()['AccountType'],
              platform: doc.data()['AccountPlatform'],
              apiKey: doc.data()['AccountPub'],
              apiSecret: doc.data()['AccountPriv']
            });
          });
        });
        // } Price Drivers

        // Bot (Single page) {
        if (this.$route.params.id) {
          var Bot = firestore
              .collection('users')
              .doc(user.uid)
              .collection('Bots')
              .doc(this.$route.params.id);

          if (await Bot.get()) {
            Bot.onSnapshot(async snapshot => {
              this.Bot = (await Bot.get()).data();
              this.Bot.id = Bot.id;
              this.Bot.symbolName = 'BTCUSDT';
            });
          }
        }
        // } Bot (Single page)

        // Interval to fix graph size {
        let i = 5;
        let afterInterval = setInterval(() => {
          if (i > 0) {
            this.onResize();
            i--;
          } else {
            clearInterval(afterInterval);
          }
        }, 300);
        // } Interval to fix graph size

        this.needsUpdate = false;
      });
    }
  }
}
</script>

<style>
@media (max-width: 768px) {
  .v-slider__tick-label {
    font-size: 12px;
  }
}
</style>